import type { TrustConfig, RegistrationEntry } from "./types";
import {
  ZERO_ADDRESS,
  normalizeAddress,
  normalizeDomain,
  toCaip10,
} from "./utils";
import type { Hex } from "./utils";
export type { Hex } from "./utils";
export { toCaip10 } from "./utils";

export type IdentityRegistryClientOptions<
  PublicClient extends PublicClientLike,
  WalletClient extends WalletClientLike | undefined = undefined
> = {
  address: Hex;
  chainId?: number;
  publicClient: PublicClient;
  walletClient?: WalletClient;
  namespace?: string;
};

export type IdentityRecord = {
  agentId: bigint;
  agentDomain: string;
  agentAddress: Hex;
};

type AgentIdentifierInput = bigint | number | string;

type RegistrationEntryParams = {
  agentId: AgentIdentifierInput;
  address: string;
  chainId: number | string;
  namespace?: string;
  signature?: string;
};

type TrustOverridesInput = Partial<
  Pick<
    TrustConfig,
    | "trustModels"
    | "validationRequestsUri"
    | "validationResponsesUri"
    | "feedbackDataUri"
  >
>;

function normalizeAgentId(agentId: AgentIdentifierInput): string {
  if (typeof agentId === "bigint") {
    if (agentId < 0n) {
      throw new Error("agentId must be non-negative");
    }
    return agentId.toString(10);
  }
  if (typeof agentId === "number") {
    if (
      !Number.isFinite(agentId) ||
      !Number.isInteger(agentId) ||
      agentId < 0
    ) {
      throw new Error("agentId must be a non-negative integer");
    }
    if (!Number.isSafeInteger(agentId)) {
      throw new Error(
        "agentId number must be a safe integer; use string or bigint for larger values"
      );
    }
    return agentId.toString(10);
  }
  const normalized = `${agentId ?? ""}`.trim();
  if (!normalized) {
    throw new Error("agentId is required");
  }
  return normalized;
}

function createRegistrationEntry(
  params: RegistrationEntryParams
): RegistrationEntry {
  const entry: RegistrationEntry = {
    agentId: normalizeAgentId(params.agentId),
    agentAddress: toCaip10({
      namespace: params.namespace,
      chainId: params.chainId,
      address: params.address,
    }),
  };
  if (params.signature) {
    entry.signature = params.signature;
  }
  return entry;
}

function createTrustConfig(
  params: RegistrationEntryParams,
  overrides?: TrustOverridesInput
): TrustConfig {
  return {
    registrations: [createRegistrationEntry(params)],
    ...overrides,
  };
}

export type IdentityRegistryClient = {
  readonly address: Hex;
  readonly chainId?: number;
  get(agentId: bigint | number | string): Promise<IdentityRecord | null>;
  resolveByDomain(domain: string): Promise<IdentityRecord | null>;
  resolveByAddress(address: Hex): Promise<IdentityRecord | null>;
  register(input: RegisterAgentInput): Promise<RegisterAgentResult>;
  update(input: UpdateAgentInput): Promise<UpdateAgentResult>;
  toRegistrationEntry(
    record: IdentityRecord,
    signature?: string
  ): RegistrationEntry;
};

export type PublicClientLike = {
  readContract(args: {
    address: Hex;
    abi: typeof IDENTITY_REGISTRY_ABI;
    functionName: IdentityRegistryReadFunctionName;
    args?: readonly unknown[];
  }): Promise<any>;
};

export type WalletClientLike = {
  account?: { address?: Hex };
  writeContract(args: {
    address: Hex;
    abi: typeof IDENTITY_REGISTRY_ABI;
    functionName: IdentityRegistryWriteFunctionName;
    args?: readonly unknown[];
  }): Promise<Hex>;
};

type IdentityRegistryReadFunctionName =
  | "Get"
  | "ResolveByDomain"
  | "ResolveByAddress";

type IdentityRegistryWriteFunctionName = "New" | "Update";

export type RegisterAgentInput = {
  domain: string;
  agentAddress?: Hex;
};

export type RegisterAgentResult = {
  transactionHash: Hex;
  agentAddress: Hex;
  agentId?: bigint;
};

export type UpdateAgentInput = {
  agentId: bigint | number | string;
  domain?: string;
  agentAddress?: Hex;
};

export type UpdateAgentResult = {
  transactionHash: Hex;
  agentId?: bigint;
};

export const IDENTITY_REGISTRY_ABI = [
  {
    type: "function",
    name: "New",
    stateMutability: "nonpayable",
    inputs: [
      { name: "agentDomain", type: "string" },
      { name: "agentAddress", type: "address" },
    ],
    outputs: [{ name: "agentId", type: "uint256" }],
  },
  {
    type: "function",
    name: "Update",
    stateMutability: "nonpayable",
    inputs: [
      { name: "agentId", type: "uint256" },
      { name: "agentDomain", type: "string" },
      { name: "agentAddress", type: "address" },
    ],
    outputs: [{ name: "ok", type: "bool" }],
  },
  {
    type: "function",
    name: "Get",
    stateMutability: "view",
    inputs: [{ name: "agentId", type: "uint256" }],
    outputs: [
      { name: "agentId", type: "uint256" },
      { name: "agentDomain", type: "string" },
      { name: "agentAddress", type: "address" },
    ],
  },
  {
    type: "function",
    name: "ResolveByDomain",
    stateMutability: "view",
    inputs: [{ name: "agentDomain", type: "string" }],
    outputs: [
      { name: "agentId", type: "uint256" },
      { name: "agentDomain", type: "string" },
      { name: "agentAddress", type: "address" },
    ],
  },
  {
    type: "function",
    name: "ResolveByAddress",
    stateMutability: "view",
    inputs: [{ name: "agentAddress", type: "address" }],
    outputs: [
      { name: "agentId", type: "uint256" },
      { name: "agentDomain", type: "string" },
      { name: "agentAddress", type: "address" },
    ],
  },
] as const;

export function createIdentityRegistryClient<
  PublicClient extends PublicClientLike,
  WalletClient extends WalletClientLike | undefined = undefined
>(
  options: IdentityRegistryClientOptions<PublicClient, WalletClient>
): IdentityRegistryClient {
  const {
    address,
    chainId,
    publicClient,
    walletClient,
    namespace = "eip155",
  } = options;

  function ensureWalletClient(): WalletClientLike {
    if (!walletClient) {
      throw new Error(
        "identity registry client requires walletClient for writes"
      );
    }
    return walletClient;
  }

  async function readRecord(
    fn: IdentityRegistryReadFunctionName,
    args: readonly unknown[]
  ): Promise<IdentityRecord | null> {
    const result = (await publicClient.readContract({
      address,
      abi: IDENTITY_REGISTRY_ABI,
      functionName: fn,
      args,
    })) as readonly [bigint, string, string];

    if (!result) return null;

    const [agentId, agentDomain, agentAddress] = result;
    if (!agentDomain && (!agentAddress || agentAddress === ZERO_ADDRESS)) {
      return null;
    }

    return {
      agentId,
      agentDomain,
      agentAddress: normalizeAddress(agentAddress),
    };
  }

  return {
    address,
    chainId,
    async get(agentId) {
      const id = BigInt(agentId);
      return readRecord("Get", [id]);
    },
    resolveByDomain(domain) {
      const normalized = normalizeDomain(domain);
      return readRecord("ResolveByDomain", [normalized]);
    },
    resolveByAddress(rawAddress) {
      const normalized = normalizeAddress(rawAddress);
      return readRecord("ResolveByAddress", [normalized]);
    },
    async register(input) {
      const wallet = ensureWalletClient();
      const normalizedDomain = normalizeDomain(input.domain);
      const normalizedAddress = normalizeAddress(
        input.agentAddress ?? wallet.account?.address ?? ZERO_ADDRESS
      );

      if (!normalizedDomain) {
        throw new Error("agent domain is required");
      }
      if (!normalizedAddress || normalizedAddress === ZERO_ADDRESS) {
        throw new Error("agent address is required");
      }

      const txHash = await wallet.writeContract({
        address,
        abi: IDENTITY_REGISTRY_ABI,
        functionName: "New",
        args: [normalizedDomain, normalizedAddress],
      });

      let agentId: bigint | undefined;
      try {
        const record = await readRecord("ResolveByDomain", [normalizedDomain]);
        agentId = record?.agentId;
      } catch {
        agentId = undefined;
      }

      return {
        transactionHash: txHash,
        agentAddress: normalizedAddress,
        agentId,
      };
    },
    async update(input) {
      const wallet = ensureWalletClient();
      const agentId = BigInt(input.agentId);
      const normalizedDomain =
        typeof input.domain === "string" ? normalizeDomain(input.domain) : "";
      const normalizedAddress =
        typeof input.agentAddress === "string"
          ? normalizeAddress(input.agentAddress)
          : ZERO_ADDRESS;

      if (
        !normalizedDomain &&
        (!normalizedAddress || normalizedAddress === ZERO_ADDRESS)
      ) {
        throw new Error("update requires domain and/or agentAddress");
      }

      const txHash = await wallet.writeContract({
        address,
        abi: IDENTITY_REGISTRY_ABI,
        functionName: "Update",
        args: [
          agentId,
          normalizedDomain,
          normalizedAddress && normalizedAddress !== ZERO_ADDRESS
            ? normalizedAddress
            : ZERO_ADDRESS,
        ],
      });

      let resultingRecord: IdentityRecord | null = null;
      try {
        if (normalizedDomain) {
          resultingRecord = await readRecord("ResolveByDomain", [
            normalizedDomain,
          ]);
        }
        if (!resultingRecord) {
          resultingRecord = await readRecord("Get", [agentId]);
        }
      } catch {
        resultingRecord = null;
      }

      return {
        transactionHash: txHash,
        agentId: resultingRecord?.agentId ?? agentId,
      };
    },
    toRegistrationEntry(record, signature) {
      if (chainId == null) {
        throw new Error(
          "identity registry client needs chainId to build CAIP-10 registration entries"
        );
      }
      return createRegistrationEntry({
        agentId: record.agentId,
        address: record.agentAddress,
        chainId,
        namespace,
        signature,
      });
    },
  };
}

export type SignAgentDomainProofOptions = {
  domain: string;
  address: Hex;
  chainId: number;
  signer: MessageSignerLike;
  nonce?: string;
};

export type MessageSignerLike =
  | {
      signMessage(message: string): Promise<string>;
    }
  | {
      signMessage(args: { message: string }): Promise<string>;
    }
  | {
      signMessage(message: unknown): Promise<string>;
    };

export async function signAgentDomainProof(
  options: SignAgentDomainProofOptions
): Promise<string> {
  const { domain, address, chainId, nonce, signer } = options;
  const normalizedDomain = normalizeDomain(domain);
  if (!normalizedDomain) throw new Error("domain is required");
  const normalizedAddress = normalizeAddress(address);
  if (!normalizedAddress || normalizedAddress === ZERO_ADDRESS) {
    throw new Error("address must be a valid hex address");
  }

  const message = buildOwnershipMessage({
    domain: normalizedDomain,
    address: normalizedAddress,
    chainId,
    nonce,
  });

  const signature = await invokeSignMessage(signer, message);
  return signature as string;
}

export function buildTrustConfigFromIdentity(
  record: IdentityRecord,
  options?: {
    signature?: string;
    chainId: number | string;
    namespace?: string;
    trustOverrides?: TrustOverridesInput;
  }
): TrustConfig {
  const chainRef = options?.chainId;
  if (chainRef == null) {
    throw new Error(
      "chainId is required to generate trust config registration entry"
    );
  }

  return createTrustConfig(
    {
      agentId: record.agentId,
      address: record.agentAddress,
      chainId: chainRef,
      namespace: options?.namespace,
      signature: options?.signature,
    },
    options?.trustOverrides
  );
}

function buildOwnershipMessage(params: {
  domain: string;
  address: Hex;
  chainId: number;
  nonce?: string;
}): string {
  const lines = [
    "ERC-8004 Agent Ownership Proof",
    `Domain: ${params.domain}`,
    `Address: ${params.address.toLowerCase()}`,
    `ChainId: ${params.chainId}`,
  ];
  if (params.nonce) {
    lines.push(`Nonce: ${params.nonce}`);
  }
  return lines.join("\n");
}

async function invokeSignMessage(
  signer: MessageSignerLike,
  message: string
): Promise<string> {
  const candidate = signer as any;
  if (typeof candidate.signMessage !== "function") {
    throw new Error("signer does not expose signMessage");
  }

  try {
    const result = await candidate.signMessage(message);
    if (typeof result === "string") return result;
  } catch (_) {
    // fall through to try structured signature
  }

  const result = await candidate.signMessage({ message });
  if (typeof result === "string") return result;
  throw new Error("signMessage did not return a signature string");
}

export type BootstrapTrustMissingContext = {
  client: IdentityRegistryClient;
  normalizedDomain: string;
};

export type BootstrapTrustOptions = {
  domain: string;
  chainId: number;
  registryAddress: Hex;
  publicClient: PublicClientLike;
  walletClient?: WalletClientLike;
  namespace?: string;
  signer?: MessageSignerLike;
  signatureNonce?: string;
  registerIfMissing?: boolean;
  skipRegister?: boolean;
  trustOverrides?: TrustOverridesInput;
  onMissing?: (
    context: BootstrapTrustMissingContext
  ) =>
    | Promise<IdentityRecord | null | undefined>
    | IdentityRecord
    | null
    | undefined;
};

export type BootstrapTrustResult = {
  trust?: TrustConfig;
  record?: IdentityRecord | null;
  transactionHash?: Hex;
  signature?: string;
  didRegister?: boolean;
};

export async function bootstrapTrust(
  options: BootstrapTrustOptions
): Promise<BootstrapTrustResult> {
  const normalizedDomain = normalizeDomain(options.domain);
  if (!normalizedDomain) {
    throw new Error("domain is required to bootstrap trust state");
  }

  const shouldRegister = Boolean(
    options.registerIfMissing && !options.skipRegister
  );

  const client = createIdentityRegistryClient({
    address: options.registryAddress,
    chainId: options.chainId,
    publicClient: options.publicClient,
    walletClient: options.walletClient,
    namespace: options.namespace,
  });

  let record = await client.resolveByDomain(normalizedDomain).catch(() => null);
  let transactionHash: Hex | undefined;
  let didRegister = false;

  if (!record && options.onMissing) {
    const handled = await options.onMissing({
      client,
      normalizedDomain,
    });
    if (handled) {
      record = handled;
    }
  }

  if (!record && shouldRegister) {
    const registration = await client.register({ domain: normalizedDomain });
    transactionHash = registration.transactionHash;
    didRegister = true;

    record = await client.resolveByDomain(normalizedDomain).catch(() => null);
    if (!record && registration.agentId != null) {
      record = {
        agentId: registration.agentId,
        agentDomain: normalizedDomain,
        agentAddress: registration.agentAddress,
      } satisfies IdentityRecord;
    }
  }

  if (!record) {
    return {
      trust: undefined,
      record: null,
      transactionHash,
      didRegister,
    };
  }

  let signature: string | undefined;
  if (options.signer) {
    signature = await signAgentDomainProof({
      domain: record.agentDomain,
      address: record.agentAddress,
      chainId: options.chainId,
      signer: options.signer,
      nonce: options.signatureNonce,
    });
  }

  const trust = buildTrustConfigFromIdentity(record, {
    chainId: options.chainId,
    namespace: options.namespace,
    signature,
    trustOverrides: options.trustOverrides,
  });

  return {
    trust,
    record,
    transactionHash,
    signature,
    didRegister,
  } satisfies BootstrapTrustResult;
}

const DEFAULT_CHAIN_ID = 84532;
const DEFAULT_NAMESPACE = "eip155";
const DEFAULT_TRUST_MODELS: NonNullable<TrustConfig["trustModels"]> = [
  "feedback",
  "inference-validation",
];

const defaultLogger = {
  info:
    typeof console !== "undefined" && typeof console.info === "function"
      ? console.info.bind(console)
      : () => {},
  warn:
    typeof console !== "undefined" && typeof console.warn === "function"
      ? console.warn.bind(console)
      : () => {},
};

function parsePositiveInteger(value: string | undefined): number | undefined {
  if (!value) return undefined;
  const parsed = Number(value);
  if (!Number.isFinite(parsed) || parsed <= 0) return undefined;
  return Math.trunc(parsed);
}

function resolveTrustOverrides(
  domain: string | undefined,
  overrides?: TrustOverridesInput,
  fallback?: TrustOverridesInput
): TrustOverridesInput | undefined {
  const result: TrustOverridesInput = {};

  if (domain) {
    result.trustModels = DEFAULT_TRUST_MODELS;
    const origin = domain.startsWith("http") ? domain : `https://${domain}`;
    result.validationRequestsUri = `${origin}/validation/requests.json`;
    result.validationResponsesUri = `${origin}/validation/responses.json`;
    result.feedbackDataUri = `${origin}/feedback.json`;
  }

  if (fallback) {
    if (fallback.trustModels !== undefined) {
      result.trustModels = fallback.trustModels;
    }
    if (fallback.validationRequestsUri !== undefined) {
      result.validationRequestsUri = fallback.validationRequestsUri;
    }
    if (fallback.validationResponsesUri !== undefined) {
      result.validationResponsesUri = fallback.validationResponsesUri;
    }
    if (fallback.feedbackDataUri !== undefined) {
      result.feedbackDataUri = fallback.feedbackDataUri;
    }
  }

  if (overrides) {
    if (overrides.trustModels !== undefined) {
      result.trustModels = overrides.trustModels;
    }
    if (overrides.validationRequestsUri !== undefined) {
      result.validationRequestsUri = overrides.validationRequestsUri;
    }
    if (overrides.validationResponsesUri !== undefined) {
      result.validationResponsesUri = overrides.validationResponsesUri;
    }
    if (overrides.feedbackDataUri !== undefined) {
      result.feedbackDataUri = overrides.feedbackDataUri;
    }
  }

  return Object.keys(result).length > 0 ? result : undefined;
}

function synthesizeTrustConfig(params: {
  address: string;
  chainId: number;
  namespace: string;
  fallbackAgentId?: number | string;
  signature?: string;
  overrides?: TrustOverridesInput;
  warn(message: string, error?: unknown): void;
}): (BootstrapTrustResult & { synthetic: true }) | null {
  try {
    const trust = createTrustConfig(
      {
        agentId: params.fallbackAgentId ?? 0,
        address: params.address,
        chainId: params.chainId,
        namespace: params.namespace,
        signature: params.signature,
      },
      params.overrides
    );

    return {
      trust,
      record: undefined,
      transactionHash: undefined,
      signature: params.signature,
      didRegister: undefined,
      synthetic: true,
    };
  } catch (error) {
    params.warn(
      "[agent-kit] unable to synthesise CAIP-10 identity entry",
      error
    );
    return null;
  }
}

type InferLogger = {
  info?(message: string): void;
  warn?(message: string, error?: unknown): void;
};

export type BootstrapIdentityClients = {
  publicClient: PublicClientLike;
  walletClient?: WalletClientLike;
  signer?: MessageSignerLike;
};

export type BootstrapIdentityClientFactory = (params: {
  chainId: number;
  rpcUrl: string;
  env: Record<string, string | undefined>;
}) =>
  | BootstrapIdentityClients
  | null
  | undefined
  | Promise<BootstrapIdentityClients | null | undefined>;

export type BootstrapIdentityOptions = {
  domain?: string;
  chainId?: number;
  registryAddress?: Hex;
  namespace?: string;
  publicClient?: PublicClientLike;
  walletClient?: WalletClientLike;
  signer?: MessageSignerLike;
  rpcUrl?: string;
  makeClients?: BootstrapIdentityClientFactory;
  registerIfMissing?: boolean;
  skipRegister?: boolean;
  signatureNonce?: string;
  trustOverrides?: TrustOverridesInput;
  fallback?: {
    address?: string;
    agentId?: number | string;
    namespace?: string;
    signature?: string;
    trustModels?: TrustConfig["trustModels"];
    validationRequestsUri?: string;
    validationResponsesUri?: string;
    feedbackDataUri?: string;
  };
  env?: Record<string, string | undefined>;
  logger?: InferLogger;
};

export type BootstrapIdentityResult = BootstrapTrustResult & {
  synthetic?: boolean;
};

export async function bootstrapIdentity(
  options: BootstrapIdentityOptions = {}
): Promise<BootstrapIdentityResult> {
  const env =
    options.env ??
    (typeof process !== "undefined" && typeof process.env === "object"
      ? (process.env as Record<string, string | undefined>)
      : {});

  const logger = {
    info: options.logger?.info ?? defaultLogger.info,
    warn: options.logger?.warn ?? defaultLogger.warn,
  } satisfies InferLogger;

  const resolvedChainId =
    options.chainId ?? parsePositiveInteger(env.CHAIN_ID) ?? DEFAULT_CHAIN_ID;

  const domain = options.domain ?? env.AGENT_DOMAIN;
  const namespace =
    options.namespace ?? options.fallback?.namespace ?? DEFAULT_NAMESPACE;
  const registryAddress =
    options.registryAddress ??
    (env.IDENTITY_REGISTRY_ADDRESS as Hex | undefined);
  const rpcUrl = options.rpcUrl ?? env.RPC_URL;

  let publicClient = options.publicClient;
  let walletClient = options.walletClient;
  let signer = options.signer;

  if (!publicClient && options.makeClients && rpcUrl) {
    const produced = await options.makeClients({
      chainId: resolvedChainId,
      rpcUrl,
      env,
    });
    if (produced?.publicClient) {
      publicClient = produced.publicClient;
      walletClient = walletClient ?? produced.walletClient;
      signer = signer ?? produced.signer ?? (produced.walletClient as any);
    }
  }

  if (!signer && walletClient) {
    signer = walletClient as any;
  }

  const fallbackOverrides = resolveTrustOverrides(domain, undefined, {
    trustModels:
      options.fallback?.trustModels ??
      (domain ? undefined : DEFAULT_TRUST_MODELS),
    validationRequestsUri: options.fallback?.validationRequestsUri,
    validationResponsesUri: options.fallback?.validationResponsesUri,
    feedbackDataUri: options.fallback?.feedbackDataUri,
  });

  const resolvedOverrides = resolveTrustOverrides(
    domain,
    options.trustOverrides,
    fallbackOverrides
  );

  if (domain && registryAddress && publicClient) {
    try {
      const result = await bootstrapTrust({
        domain,
        chainId: resolvedChainId,
        registryAddress,
        namespace,
        publicClient,
        walletClient,
        signer,
        signatureNonce: options.signatureNonce ?? env.IDENTITY_SIGNATURE_NONCE,
        registerIfMissing:
          options.registerIfMissing ?? env.REGISTER_IDENTITY === "true",
        skipRegister: options.skipRegister,
        trustOverrides: resolvedOverrides,
      });

      if (result.trust) {
        return result;
      }

      logger.warn(
        "[agent-kit] identity not found in registry; falling back to synthetic trust"
      );
    } catch (error) {
      logger.warn(
        "[agent-kit] failed to bootstrap ERC-8004 trust; falling back to synthetic trust",
        error
      );
    }
  }

  const fallbackAddress = options.fallback?.address ?? env.ADDRESS;
  if (!fallbackAddress) {
    logger.warn(
      "[agent-kit] unable to resolve fallback identity address; skipping trust metadata"
    );
    return {};
  }

  const fallbackSignature =
    options.fallback?.signature ??
    env.IDENTITY_FALLBACK_SIGNATURE ??
    "0xdeadbeef";

  const synthetic = synthesizeTrustConfig({
    address: fallbackAddress,
    chainId: resolvedChainId,
    namespace,
    fallbackAgentId: options.fallback?.agentId,
    signature: fallbackSignature,
    overrides: resolvedOverrides,
    warn: logger.warn,
  });

  if (synthetic) {
    return synthetic;
  }

  return {};
}

export type MakeViemClientsFromEnvOptions = {
  env?: Record<string, string | undefined>;
  rpcUrl?: string;
  privateKey?: `0x${string}` | string;
};

async function importViemModules(): Promise<{
  createPublicClient: (...args: any[]) => any;
  createWalletClient: (...args: any[]) => any;
  http: (url: string) => any;
  privateKeyToAccount: (key: `0x${string}`) => any;
  baseSepolia: { id: number } & Record<string, unknown>;
} | null> {
  try {
    const viem = await import("viem");
    const accounts = await import("viem/accounts");
    const chains = await import("viem/chains").catch(() => ({}));
    const baseSepoliaChain =
      (chains as any).baseSepolia ?? ({ id: DEFAULT_CHAIN_ID } as const);
    return {
      createPublicClient: (viem as any).createPublicClient,
      createWalletClient: (viem as any).createWalletClient,
      http: (viem as any).http,
      privateKeyToAccount: (accounts as any).privateKeyToAccount,
      baseSepolia: baseSepoliaChain,
    };
  } catch (error) {
    defaultLogger.warn(
      "[agent-kit] viem helpers unavailable; install viem to use makeViemClientsFromEnv",
      error
    );
    return null;
  }
}

function resolveEnvObject(
  env?: Record<string, string | undefined>
): Record<string, string | undefined> {
  if (env) return env;
  if (typeof process !== "undefined" && typeof process.env === "object") {
    return process.env as Record<string, string | undefined>;
  }
  return {};
}

export async function makeViemClientsFromEnv(
  options: MakeViemClientsFromEnvOptions = {}
): Promise<BootstrapIdentityClientFactory | undefined> {
  const env = resolveEnvObject(options.env);
  const modules = await importViemModules();
  if (!modules) return undefined;

  return ({ chainId, rpcUrl, env: runtimeEnv }) => {
    const effectiveRpcUrl = options.rpcUrl ?? rpcUrl ?? env.RPC_URL;
    if (!effectiveRpcUrl) {
      defaultLogger.warn(
        "[agent-kit] RPC_URL missing for viem client factory; skipping"
      );
      return null;
    }

    const transport = modules.http(effectiveRpcUrl);
    const chain = { ...modules.baseSepolia, id: chainId };
    const publicClient = modules.createPublicClient({ chain, transport });

    const mergedEnv = {
      ...env,
      ...runtimeEnv,
    };
    const privateKey = (options.privateKey ?? mergedEnv.PRIVATE_KEY) as
      | `0x${string}`
      | undefined;

    let walletClient: any = undefined;
    if (privateKey) {
      try {
        const account = modules.privateKeyToAccount(
          privateKey as `0x${string}`
        );
        walletClient = modules.createWalletClient({
          chain,
          account,
          transport,
        });
      } catch (error) {
        defaultLogger.warn(
          "[agent-kit] failed to configure viem wallet client from PRIVATE_KEY",
          error
        );
      }
    }

    return {
      publicClient,
      walletClient,
      signer: walletClient,
    } satisfies BootstrapIdentityClients;
  };
}
